<template>
  <div>
    <div class="navlist">
      <div
        class="item"
        :class="{ active: active == item.id }"
        v-for="item in navList"
        :key="item.id"
        @click="change(item.id)"
      >
        {{ item.name }}
      </div>
    </div>
    <!-- 列表渲染 -->
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
    >
      <div class="goodslist">
        <div
          class="goods"
          v-for="item in goodsList"
          :key="item.goodsId"
          @click="gotoDetail(item)"
        >
          <div class="goodsimg">
            <img :src="item.img" />
          </div>
          <div class="goodsinfo">
            <div class="name">
              {{ item.name }}
            </div>
            <div class="price">
              <div class="text">
                ¥<span>{{ item.price }}</span>
              </div>
              <div class="btn">购买</div>
            </div>
          </div>
        </div>
      </div>
    </van-list>
  </div>
</template>

<script>
import homeModel from "@/model/homeModel";
import { mapMutations } from "vuex";
export default {
  props: ["ischange"],
  data() {
    return {
      active: 1,
      navList: [
        { id: 1, name: "销售排行", type: "salesRank" },
        { id: 2, name: "猜你喜欢", type: "youLove" },
        { id: 3, name: "热门推荐", type: "hotRecommend" },
        { id: 4, name: "优选劲爆", type: "selective" },
      ],
      goodsList: [],
      loading: false,
      finished: false,
      refreshing: false,
      pageIndex: 1,
    };
  },
  watch: {
    ischange() {
      this.onRefresh();
    },
  },
  methods: {
    ...mapMutations(["setGoodsInfo"]),
    change(id) {
      this.active = id;
      this.onRefresh();
    },
    async onLoad() {
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求
      let type = this.navList.filter((item) => item.id == this.active)[0].type;
      let { data: list } = await homeModel[type]({
        _page: this.pageIndex,
        _limit: 10,
      });

      this.loading = false;
      this.goodsList = [...this.goodsList, ...list];
      this.pageIndex += 1;
      // 加载状态结束
      this.$emit("changeRefreshing");
      // 数据全部加载完成
      if (list.length < 10) {
        this.finished = true;
      }
    },
    onRefresh() {
      // 清空列表数据
      this.finished = false;
      (this.goodsList = []), (this.pageIndex = 1);

      // 重新加载数据
      // 将 loading 设置为 true，表示处于加载状态
      this.loading = true;
      this.onLoad();
    },
    gotoDetail(info) {
      this.$router.push("/detail/"+info.goodsId);
    },
  },
};
</script>

<style lang="scss" scoped>
.active {
  font-weight: 700;
  color: #e84040;
  background: url(@/assets/image/bBar.png) no-repeat;
  background-size: 40px 20px;
  background-position: 10px 25px;
}
.navlist {
  width: 100%;
  height: 40px;
  text-align: center;
  font-size: 15px;
  color: #979891;
  display: flex;
  justify-content: space-evenly;
}
.goodslist {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  .goods {
    width: 175px;
    height: 275px;
    border-radius: 8px;
    overflow: hidden;
    background: #fff;
    margin-bottom: 5px;
    .goodsimg {
      width: 175px;
      height: 175px;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .goodsinfo {
      width: 100%;
      height: 100px;
      box-sizing: border-box;
      padding: 10px;
      .name {
        width: 100%;
        height: 44px;
        overflow: hidden;
        line-height: 22px;
        display: -webkit-box;
        text-overflow: ellipsis;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        font-size: 14px;
        color: #363131;
      }
      .price {
        width: 100%;
        height: 20px;
        margin-top: 15px;
        font-size: 14px;
        display: flex;
        .text {
          flex: 1;
          color: #e84040;
          span {
            font-size: 15px;
          }
        }
        .btn {
          width: 40px;
          height: 20px;
          border: 1px solid #ea4141;
          border-radius: 10px;
          font-size: 12px;
          box-sizing: border-box;
          color: #e84040;
          text-align: center;
          line-height: 18px;
        }
      }
    }
  }
}
</style>